<template>
  <div class="phoneContainer">
    <div class="logoContainer">
      <img class="logoPoorba" src="../assets/.png" />
    </div>
    <h2 id="firstTitle"></h2>
    <form @submit.prevent="handleLogin">
      <input
        type="text"
        v-model="formattedPhoneNumber"
        name="username"
        class="inputBar"
        placeholder=" "
        @input="formatPhoneNumber"
        maxlength="13"
        required
      />
      <input
        type="submit"
        id="color1"
        class="longCLickBtn"
        value=""
      />
    </form>
    <form class="bottomFirst">
      <h3 class="subTitleLeft"> ?</h3>
      <router-link to="/cr"
        ><input type="submit" class="longCLickBtn" value=""
      /></router-link>
    </form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "FirstIn",
  components: {},
  data() {
    return {
      currentTime: null,
      phoneNumber: "", //     
      formattedPhoneNumber: "", //  
      validPhoneNumbers: [], //   
    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
    axios
      .get("/purba1/phoneNumbers.json")
      .then((response) => {
        this.validPhoneNumbers = response.data.phoneNumbers;
      })
      .catch((error) => {
        console.error("     :", error);
      });
  },
  methods: {
    //     
    formatPhoneNumber() {
      //  
      this.phoneNumber = this.formattedPhoneNumber.replace(/\D/g, "");

      //    
      if (this.phoneNumber.length < 4) {
        this.formattedPhoneNumber = this.phoneNumber;
      } else if (this.phoneNumber.length < 8) {
        this.formattedPhoneNumber = `${this.phoneNumber.slice(
          0,
          3
        )}-${this.phoneNumber.slice(3)}`;
      } else {
        this.formattedPhoneNumber = `${this.phoneNumber.slice(
          0,
          3
        )}-${this.phoneNumber.slice(3, 7)}-${this.phoneNumber.slice(7)}`;
      }
    },
    handleLogin() {
      //   
      if (this.validPhoneNumbers.includes(this.formattedPhoneNumber)) {
        //     Vue 
        this.$router.push("/main"); // 'next-page'   
      } else {
        alert("  .");
      }
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
  },
};
</script>

<style>
@import "../css/index.css";
</style>
