<template>
  <div id="app" class="backgroundContainer">
    <div class="phoneContainer">
      <div class="withBack">
        <p class="pageTitle">   </p>
        <img
          role="button"
          tabindex="0"
          alt=""
          @click="[goBack(), stopSpeech()]"
          @keyup="goBack"
          class="backImg"
          src="../assets/.png"
        />
      </div>

      <!--   -->
      <div class="noticeContent" style="margin-top: 20px">
        <!--    -->
        <p class="writtenDate">2024.10.15 </p>
        <!--   -->
        <h2 style="text-align: center"> 254  </h2>
        <p :style="{ fontSize: fontSz + 'px' }">
          <b>:</b> 2024 10 16
        </p>
        <p :style="{ fontSize: fontSz + 'px' }">
          <b>:</b>  254     , 
            .
        </p>
        <p :style="{ fontSize: fontSz + 'px' }">
          <b>:</b>  11~2
        </p>
        <p :style="{ fontSize: fontSz + 'px' }">
          <b>:</b>    254
        </p>
        <div class="buttonGroup1">
          <button class="shortBtn1" @click="upFontSize">+ </button>
          <button class="shortBtn1" @click="downFontSize">- </button>
        </div>
      </div>

      <!--    -->
      <div class="buttonContainer">
        <RouterLink to="/main">
          <button class="addToCalendarBtn" @click="stopSpeech">
             
          </button>
        </RouterLink>
      </div>
      <div class="btnBundles2">
        <button class="mainBtn b" @click="speakText">
          <img class="mainIcon" src="../assets/.png" />
          <span class="mainTxt"></span>
        </button>
        <button class="mainBtn b" @click="stopSpeech">
          <img class="mainIcon" src="../assets/.png" />
          <span class="mainTxt"></span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "AnPage3",
  components: {},
  data() {
    return {
      fontSz: 16,
      currentTime: null,
      volume: 1.0, //  
      rate: 1.0, //  
    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
  },
  computed: {},
  methods: {
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    goBack() {
      this.$router.back();
    },
    upFontSize() {
      this.fontSz += 1; //   1px 
    },
    downFontSize() {
      if (this.fontSz > 1) {
        this.fontSz -= 1; //   1px ,  1px 
      }
    },
    speakText() {
      const text = `
       254  
      : 2024 10 16
      :  254     ,    .
      :  11~2
      :    254
      `;
      const utterance = new SpeechSynthesisUtterance(text);
      utterance.volume = this.volume; //  
      utterance.rate = this.rate; //  
      utterance.lang = "ko-KR"; //  
      speechSynthesis.speak(utterance);
    },
    stopSpeech() {
      speechSynthesis.cancel(); //     
    },
  },
};
</script>

<style>
/* index.css  import */
@import "../css/index.css"; /*  */

/*  :   */
.topBar {
  display: flex;
  justify-content: center; /*    */
  align-items: center;
  margin-top: 10px;
}

.pageTitle {
  font-size: 24px;
  font-weight: bold;
  margin-top: 7px;
  text-align: center;
}
/*   */

/*    */
.noticeContent {
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 7px;
  margin-bottom: 15px; /*     */
  position: relative; /*      */
}
/*   */
.writtenDate {
  position: absolute; /*      */
  top: -15px; /*    */
  right: 10px; /*     */
  color: #666; /*   */
  font-size: 14px; /*    */
}
.noticeContent h2 {
  font-weight: bold;
  margin-bottom: 30px;
}

.noticeContent p {
  margin-top: 20px;
  margin-bottom: 10px;
}

/*     */
.viewDetailsBtn {
  margin-top: 20px;
  margin-right: 10px;
  padding: 8px 16px;
  border: none;
  background-color: #007bff;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
}

/*     */
.addToCalendarBtn {
  margin-top: 20px; /*      */
  padding: 12px 24px; /*   */
  border: none;
  background-color: #28a745; /*   */
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
}

/*    */
.buttonContainer {
  display: flex;
  justify-content: center;
}
</style>
