<template>
  <div class="phoneContainer">
    <div class="loading-modal" v-if="isLoading == 1">
      <div class="loading-content">
        <!--   -->
        <img :src="currentImg" alt="Dinosaur running" class="dino-image" />

        <!--   -->
        <div class="progress-bar">
          <div class="progress-fill" :style="{ width: progress + '%' }"></div>
        </div>

        <h2> ..</h2>
      </div>
    </div>
    <div class="modalBack" v-if="ModalWindow1 == 1">
      <div class="modalWindow">
        <h2 class="word2"> ?</h2>
        <div class="modalGroup">
          <button class="modalBtn" @click="vueClick('CameraSummary')">
            
          </button>
          <button class="modalBtn" @click="vueClick('CameraVoice')">
            
          </button>
        </div>
      </div>
    </div>
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="goBack"
        @keyup="goBack"
        class="backImg"
        src="../assets/.png"
      />
      <h2 class="word1"> </h2>
    </div>
    <div class="captureContainer">
      <img
        v-if="imageDataUrl"
        :src="imageDataUrl"
        alt=" "
        class="captureDisplay"
      />
    </div>
    <div class="buttonGroup1">
      <router-link to="/cm">
        <button class="captureBtn2"></button>
      </router-link>
      <button @click="ModalWindow1 = 1" class="captureBtn2"></button>
    </div>
  </div>
</template>

<script>
import { useImageStore } from "../stores/imageStore";

export default {
  name: "CameraImage",
  components: {},
  data() {
    return {
      currentTime: null,
      imageDataUrl: useImageStore().imageDataUrl,
      ModalWindow1: 0,
      isLoading: 0,
      progress: 0, //  
      dinoImages: [
        require("@/assets/dinorun1.png"), //    
        require("@/assets/dinorun2.png"), //    
      ],
      currentImg: require("@/assets/dinorun1.png"),
      currentIdx: 0,
    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
  },
  methods: {
    vueClick(vueName) {
      this.isLoading = 1;
      this.ModalWindow1 = 0;
      this.startLoading();
      this.startImageRotation(); //   

      // 10    
      setTimeout(() => {
        this.isLoading = 0;
        this.$router.push({ name: vueName }); //   
      }, 22000); //22
    },
    startLoading() {
      this.progress = 0;
      const interval = setInterval(() => {
        if (this.progress < 100) {
          this.progress += 100 / 22; // 1% 
        } else {
          clearInterval(interval);
        }
      }, 1000); // 1 10%,  10  100% 
    },
    startImageRotation() {
      this.imageInterval = setInterval(() => {
        this.currentIdx = (this.currentIdx + 1) % 2;
        this.currentImg = this.dinoImages[this.currentIdx]; //   
      }, 1000); // 1  
    },
    goBack() {
      this.$router.back();
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
  },
  beforeUnmount() {
    clearInterval(this.imageInterval); //      
  },
};
</script>

<style>
@import "../css/index.css";
</style>
