<template>
  <div class="phoneContainer">
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="goBack"
        @keyup="goBack"
        class="backImg"
        src="../assets/.png"
      />
      <h2 class="word1">  !</h2>
    </div>
    <div class="btnLayout">
      <div class="barDiv">
        <img class="mainIcon2" src="../assets/.png" />
        <br />
        <input
          type="range"
          id="volume"
          v-model="volume"
          min="0"
          max="2.0"
          step="0.1"
        />
        <br />
        <div class="buttonGroup2">
          <button
            class="shortBtn2"
            @click="downVoice"
            :style="{ fontSize: '24px' }"
          >
            
          </button>
          <button
            class="shortBtn2"
            @click="upVoice"
            :style="{ fontSize: '24px' }"
          >
            
          </button>
        </div>
      </div>
      <div class="barDiv">
        <img class="mainIcon2" src="../assets/.png" />
        <br />
        <input
          type="range"
          id="rate"
          v-model="rate"
          min="0.1"
          max="1.5"
          step="0.1"
        />
        <br />
        <div class="buttonGroup2">
          <button
            class="shortBtn2"
            @click="slowVoice"
            :style="{ fontSize: '24px' }"
          >
            
          </button>
          <button
            class="shortBtn2"
            @click="fastVoice"
            :style="{ fontSize: '24px' }"
          >
            
          </button>
        </div>
      </div>

      <div class="btnBundles2">
        <button class="mainBtn b" @click="speakText">
          <img class="mainIcon" src="../assets/.png" />
          <span class="mainTxt"></span>
        </button>
        <button class="mainBtn b" @click="stopSpeech">
          <img class="mainIcon" src="../assets/.png" />
          <span class="mainTxt"></span>
        </button>
      </div>
    </div>

    <router-link to="/cs">
      <button class="longBtn1 b" @click="stopSpeech"></button>
    </router-link>
    <router-link to="/main">
      <button class="longBtn1 b" @click="stopSpeech"></button>
    </router-link>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "CameraVoice",
  components: {},
  data() {
    return {
      jsonData: {}, //    
      currentTime: null,
      fontSize: 16,
      volume: 1.0, //  
      rate: 1.0, //  
    };
  },
  async mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
    axios
      .get("/purba1/out2.txt")
      .then((response) => {
        //   JSON 
        this.jsonData = JSON.parse(response.data);
      })
      .catch((error) => {
        console.error("    :", error);
      });
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    upVoice() {
      if (this.volume < 2) {
        this.volume = Math.min(this.volume + 0.1, 2);
      }
    },
    downVoice() {
      if (this.volume > 0) {
        this.volume = Math.max(this.volume - 0.1, 0);
      }
    },
    fastVoice() {
      if (this.rate < 1.5) {
        this.rate = Math.min(this.rate + 0.1, 1.5);
      }
    },
    slowVoice() {
      if (this.rate > 0.1) {
        this.rate = Math.max(this.rate - 0.1, 0.1);
      }
    },
    speakText() {
      if (!this.jsonData.title) {
        console.error("JSON   .");
        return;
      }
      const text = `
        : ${this.jsonData.title || "  "},
        : ${this.jsonData.start_day || "  "} ${
        "" + this.jsonData.end_day + "" || "  "
      },
        : ${this.jsonData.location || "  "},
        : ${this.jsonData.price || "  "},
        : ${this.jsonData.time || "  "},
        : ${this.jsonData.summary || "  "},
        : ${this.jsonData.etc || "  "}
      `;
      const utterance = new SpeechSynthesisUtterance(text);
      utterance.volume = this.volume; //  
      utterance.rate = this.rate; //  
      utterance.lang = "ko-KR"; //  
      speechSynthesis.speak(utterance);
    },
    stopSpeech() {
      speechSynthesis.cancel(); //     
    },
  },
};
</script>

<style>
@import "../css/index.css";
</style>
