<template>
  <div class="phoneContainer">
    <!--  :  -->
    <div class="withBack">
      <p class="pageTitle"> </p>
      <RouterLink to="/main">
        <img
          role="button"
          tabindex="0"
          alt=""
          class="backImg"
          src="../assets/.png"
        />
      </RouterLink>
    </div>
    <!--   -->
    <!--   -->
    <div class="searchBarContainer">
      <input
        type="text"
        v-model="searchKeyword"
        placeholder=" "
        class="searchBar"
      />
      <button @click="search" class="searchButton"></button>
    </div>
    <!--   -->
    <div class="noticeBoard">
      <div
        class="noticeItem"
        v-for="(notice, index) in visibleNotices"
        :key="index"
      >
        <!-- An1.vue    -->
        <p class="noticeTitle" @click="goToAn(notice.title)">
          {{ notice.title }}
        </p>
      </div>
    </div>
    <!--   -->
    <div class="boardNavigation">
      <!--     -->
      <button @click="prevPage">&lt;</button>
      &nbsp;&nbsp;2&nbsp;&nbsp;
      <!--     -->
      <button @click="nextPage">&gt;</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "AnnounceList2",
  components: {},
  data() {
    return {
      currentTime: null,
      currentPage: 1,
      itemsPerPage: 5,
      totalPages: 0,
      searchKeyword: "",
      notices: [
        {
          title: "[]    ",
        },
        {
          title: "[]   ",
        },
        {
          title: "[]    ",
        },
        {
          title: "[]   ",
        },
      ],
    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
  },
  computed: {
    visibleNotices() {
      const startIndex = (this.currentPage - 1) * this.itemsPerPage;
      const endIndex = startIndex + this.itemsPerPage;
      return this.notices.slice(startIndex, endIndex);
    },
  },
  methods: {
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    goBack() {
      this.$router.back();
    },
    prevPage() {
      this.$router.push({ name: "AnnounceList" });
    },
    nextPage() {
      this.$router.push({ name: "AnnounceList" });
    },
    search() {
      console.log(":", this.searchKeyword);
      //      .
    },
    goToAn(title) {
      if (title === "[]     ") {
        this.$router.push({ name: "AnPage1" });
      }
    },
  },
};
</script>

<style>
/* index.css  import */
@import "../css/index.css"; /*  */
</style>
