<template>
  <div class="phoneContainer">
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="goBack"
        @keyup="goBack"
        class="backImg"
        src="../assets/.png"
      />
      <h2 class="word1">  !</h2>
    </div>
    <div class="infDiv">
      <h1 class="pre-wrap" :style="{ fontSize: fontSize + 'px' }">
        {{ jsonData.title || " " }}
      </h1>
      <p class="pre-wrap" :style="{ fontSize: fontSize + 'px' }">
        <b>:</b> {{ jsonData.start_day || " " }} ~
        {{ jsonData.end_day || " " }}
      </p>
      <p class="pre-wrap" :style="{ fontSize: fontSize + 'px' }">
        <b>:</b> {{ jsonData.location || "  " }}
      </p>
      <p class="pre-wrap" :style="{ fontSize: fontSize + 'px' }">
        <b>:</b> {{ jsonData.price || "  " }}
      </p>
      <p class="pre-wrap" :style="{ fontSize: fontSize + 'px' }">
        <b>:</b> {{ jsonData.time || "  " }}
      </p>
      <p class="pre-wrap" :style="{ fontSize: fontSize + 'px' }">
        <b>:</b> {{ jsonData.summary || "  " }}
      </p>
      <p class="pre-wrap" :style="{ fontSize: fontSize + 'px' }">
        <b>:</b> {{ jsonData.etc || "  " }}
      </p>
    </div>
    <div class="buttonGroup1">
      <button class="shortBtn1" @click="upFontSize">+ </button>
      <button class="shortBtn1" @click="downFontSize">- </button>
    </div>
    <router-link to="/cv">
      <button class="longBtn1 b"></button>
    </router-link>
    <router-link to="/main">
      <button class="longBtn1 b"></button>
    </router-link>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "CameraSummary",
  components: {},
  data() {
    return {
      jsonData: {},
      currentTime: null,
      fontSize: 16,
    };
  },
  async mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
    // json_output.txt  axios 
    axios
      .get("/purba1/out2.txt") // public    
      .then((response) => {
        //    jsonData 
        this.jsonData = JSON.parse(response.data);
      })
      .catch((error) => {
        console.error("    :", error);
      });
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    upFontSize() {
      this.fontSize += 1; //   1px 
    },
    downFontSize() {
      if (this.fontSize > 1) {
        this.fontSize -= 1; //   1px ,  1px 
      }
    },
  },
};
</script>

<style>
@import "../css/index.css";
</style>
