<template>
  <div class="phoneContainer">
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="goBack"
        @keyup="goBack"
        class="backImg"
        src="../assets/.png"
      />
    </div>
    <div>
      <video
        ref="videoElement"
        class="cameraDisplay"
        autoplay
        playsinline
      ></video>
      <br /><br />
      <button class="captureBtn c" @click="toggleCamera">
         (: {{ facingMode === "user" ? "" : "" }})
      </button>
      <router-link to="/ci">
        <button class="captureBtn c" @click="capture"></button>
      </router-link>
      <canvas ref="canvas" style="display: none"></canvas>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onBeforeUnmount } from "vue";
import { useImageStore } from "../stores/imageStore";
import { useRouter } from "vue-router";

export default {
  name: "CameraFirst",
  setup() {
    //  
    const videoElement = ref(null);
    const canvas = ref(null);
    const currentStream = ref(null);
    const facingMode = ref("environment"); // 'user' , 'environment' 
    const imageStore = useImageStore(); // Pinia  
    const router = useRouter();

    /**
     *    
     */
    const getCameraStream = async () => {
      if (currentStream.value) {
        currentStream.value.getTracks().forEach((track) => track.stop());
      }

      try {
        const constraints = {
          video: {
            facingMode: facingMode.value,
          },
        };
        const stream = await navigator.mediaDevices.getUserMedia(constraints);
        currentStream.value = stream;
        if (videoElement.value) {
          videoElement.value.srcObject = stream;
        }
      } catch (error) {
        console.error("     :", error);
        // fallback : facingMode    
        try {
          const fallbackStream = await navigator.mediaDevices.getUserMedia({
            video: true,
            audio: false,
          });
          currentStream.value = fallbackStream;
          if (videoElement.value) {
            videoElement.value.srcObject = fallbackStream;
          }
        } catch (fallbackError) {
          console.error("Fallback    :", fallbackError);
          alert("   .  .");
        }
      }
    };

    const goBack = () => {
      router.back();
    };

    /**
     * /   
     */
    const toggleCamera = () => {
      facingMode.value = facingMode.value === "user" ? "environment" : "user";
      getCameraStream();
    };

    /**
     *   ,   
     */
    const capture = () => {
      const video = videoElement.value;
      const canvasElement = canvas.value;

      //   
      canvasElement.width = video.videoWidth;
      canvasElement.height = video.videoHeight;

      //    
      canvasElement
        .getContext("2d")
        .drawImage(video, 0, 0, canvasElement.width, canvasElement.height);

      //    
      saveImage(canvasElement);

      //  URL    
      const imageDataUrl = canvasElement.toDataURL("image/png");
      imageStore.setImageDataUrl(imageDataUrl);

      //   
      canvasElement.toBlob((blob) => {
        const formData = new FormData();
        formData.append("image", blob, "purbaCapture.png");

        fetch("http://localhost:8080/upload", {
          //    .
          method: "POST",
          body: formData,
        })
          .then((response) => response.text())
          .then((data) => console.log(" :", data))
          .catch((error) => console.error("  :", error));
      });
    };

    /**
     *   
     */
    const saveImage = (canvasElement) => {
      const imageDataUrl = canvasElement.toDataURL("image/png");

      //   
      const link = document.createElement("a");
      link.href = imageDataUrl;
      link.download = "capture.png"; //   
      document.body.appendChild(link); //  DOM 
      link.click(); //   ( )
      document.body.removeChild(link); //  
    };

    //      
    onMounted(() => {
      getCameraStream();
    });

    //     
    onBeforeUnmount(() => {
      if (currentStream.value) {
        currentStream.value.getTracks().forEach((track) => track.stop());
      }
    });

    //    
    return {
      videoElement,
      canvas,
      toggleCamera,
      capture,
      facingMode,
      goBack,
    };
  },
};
</script>

<style>
@import "../css/index.css";
</style>
