<template>
  <div class="phoneContainer">
    <div class="modal-overlay" v-if="ModalW == 1">
      <div class="modal-content">
        <img class="imgblk3" src="../assets/img2.jpeg" @click="ModalW = 0" />
      </div>
    </div>
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="[goBack(), stopSpeech()]"
        @keyup="goBack"
        class="backImg"
        src="../assets/.png"
      />
      <h2 class="word1">2024 11 1</h2>
    </div>
    <div class="imgDisplayed">
      <img class="imgblk2" src="../assets/img2.jpeg" @click="ModalW = 1" />
    </div>
    <div class="infDiv">
      <h1 class="festD" :style="{ fontSize: fontSz + 2 + 'px' }">
          
      </h1>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>:</b> 2022-10-10 ~ 2022-10-23
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>:</b> 
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>:</b>   
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>:</b>   
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>:</b>   ' '  
         10 10 23 2 .  
               
        .
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>:</b>      6 
           ,   
        (031-8006-7424)   .
      </p>
      <div class="buttonGroup1">
        <button class="shortBtn1" @click="upFontSize">+ </button>
        <button class="shortBtn1" @click="downFontSize">- </button>
      </div>
    </div>
    <div class="btnBundles2">
      <button class="mainBtn b" @click="speakText">
        <img class="mainIcon" src="../assets/.png" />
        <span class="mainTxt"></span>
      </button>
      <button class="mainBtn b" @click="stopSpeech">
        <img class="mainIcon" src="../assets/.png" />
        <span class="mainTxt"></span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "GallerySummary2",
  components: {},
  data() {
    return {
      currentTime: null,
      fontSz: 16,
      ModalW: 0,
      volume: 1.0, //  
      rate: 1.0, //  
    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    upFontSize() {
      this.fontSz += 1; //   1px 
    },
    downFontSize() {
      if (this.fontSz > 1) {
        this.fontSz -= 1; //   1px ,  1px 
      }
    },
    speakText() {
      const text = `
        ,
      : 2022-10-10 ~ 2022-10-23,
      : ,
      :   ,
      :   ,
      :   ' '    10 10 23 2 .           .,
      :      6     ,    (031-8006-7424)   .
      `;
      const utterance = new SpeechSynthesisUtterance(text);
      utterance.volume = this.volume; //  
      utterance.rate = this.rate; //  
      utterance.lang = "ko-KR"; //  
      speechSynthesis.speak(utterance);
    },
    stopSpeech() {
      speechSynthesis.cancel(); //     
    },
  },
};
</script>

<style>
@import "../css/index.css";
</style>
