<template>
  <div class="phoneContainer">
    <div class="withBack">
      <RouterLink to="/main">
        <img
          role="button"
          tabindex="0"
          alt=""
          class="backImg"
          src="../assets/.png"
        />
      </RouterLink>
    </div>
    <div class="calendar-container">
      <div class="calendar-header">
        <button @click="previousMonth" class="dayBtn"></button>
        <span
          :style="{ color: '#1f4e79', fontSize: '22px', fontWeight: 'bold' }"
          >{{ formattedCurrentMonth }}</span
        >
        <button @click="nextMonth" class="dayBtn"></button>
      </div>
      <div class="calendar-days">
        <div class="day-name" v-for="day in dayNames" :key="day">
          {{ day }}
        </div>
        <div
          v-for="(date, index) in calendarDays"
          :key="index"
          class="calendar-date"
          :class="{
            'is-today': isToday(date),
            'is-selected':
              selectedDate && date && date.getTime() === selectedDate.getTime(),
            'has-note': date && hasNote(date), //     
          }"
          @click="selectDate(date)"
        >
          <span v-if="date">{{ date.getDate() }}</span>
        </div>
      </div>
    </div>
    <!--   -->
    <div v-if="selectedDate" class="note-section">
      <p :style="{ color: '#1f4e79', fontSize: '18px', fontWeight: 'bold' }">
        {{ formattedSelectedDate }} 
      </p>
      <textarea v-model="note" placeholder=" !"></textarea>
      <button @click="saveNote" class="dayBtn"></button>
    </div>
  </div>
</template>

<script>
export default {
  name: "CalanderPage",
  components: {},
  data() {
    return {
      currentDate: new Date(),
      selectedDate: null,
      note: "",
      notes: {
        "Sun Oct 20 2024":
          "-   \n- 2    ",
        "Wed Oct 16 2024": "-  254  ",
        "Sat Nov 16 2024": "-   ",
      }, //    
    };
  },
  computed: {
    formattedCurrentMonth() {
      return `${this.currentDate.getFullYear()} ${
        this.currentDate.getMonth() + 1
      }`;
    },
    formattedSelectedDate() {
      if (!this.selectedDate) return "";
      return `${this.selectedDate.getFullYear()} ${
        this.selectedDate.getMonth() + 1
      } ${this.selectedDate.getDate()}`;
    },
    dayNames() {
      return ["", "", "", "", "", "", ""];
    },
    calendarDays() {
      const startOfMonth = new Date(
        this.currentDate.getFullYear(),
        this.currentDate.getMonth(),
        1
      );
      const endOfMonth = new Date(
        this.currentDate.getFullYear(),
        this.currentDate.getMonth() + 1,
        0
      );
      const days = [];

      //      
      for (let i = 0; i < startOfMonth.getDay(); i++) {
        days.push(null);
      }

      //    
      for (let i = 1; i <= endOfMonth.getDate(); i++) {
        days.push(
          new Date(
            this.currentDate.getFullYear(),
            this.currentDate.getMonth(),
            i
          )
        );
      }

      return days;
    },
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    previousMonth() {
      this.currentDate.setMonth(this.currentDate.getMonth() - 1);
      this.currentDate = new Date(this.currentDate); //     Date  
    },
    nextMonth() {
      this.currentDate.setMonth(this.currentDate.getMonth() + 1);
      this.currentDate = new Date(this.currentDate); //     Date  
    },
    isToday(date) {
      if (!date) return false;
      const today = new Date();
      return (
        date.getDate() === today.getDate() &&
        date.getMonth() === today.getMonth() &&
        date.getFullYear() === today.getFullYear()
      );
    },
    selectDate(date) {
      this.selectedDate = date;
      this.note = this.notes[date.toDateString()] || ""; //    
    },
    saveNote() {
      this.notes[this.selectedDate.toDateString()] = this.note; //  
      alert(`${this.formattedSelectedDate}  .`);
    },
    hasNote(date) {
      return !!this.notes[date.toDateString()]; //   
    },
  },
};
</script>

<style>
/* index.css  import */
@import "../css/index.css"; /*  */
.calendar-container {
  width: 100%;
  max-width: 400px;
  margin: 10px auto;
  text-align: center;
}
.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  font-size: 1.2em;
}
.calendar-days {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 5px;
}
.day-name {
  font-weight: bold;
}
.calendar-date {
  padding: 10px;
  text-align: center;
  background-color: #f0f0f0;
  border-radius: 4px;
}
.calendar-date.is-today {
  background-color: #ffeb3b;
}
.calendar-date.is-selected {
  background-color: #add8e6;
}

.calendar-date.has-note {
  background-color: #ffcccb; /*     */
}
.note-section {
  margin-top: 20px;
  text-align: center;
}

textarea {
  width: 100%;
  height: 100px;
  margin-top: 10px;
}

.dayBtn {
  padding: 8px 24px; /*   */
  border: none;
  font-size: 18px;
  font-weight: bold;
  background-color: rgb(37, 78, 121); /*   */
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /*    */
}
</style>
