      <img class="logoPoorba" src="../assets/.png" />
      <RouterLink to="/">
        <img
          role="button"
          tabindex="0"
          alt=""
          class="backImg"
          src="../assets/.png"
        />
      </RouterLink>
    </div>
    <div class="btnBundles">
      <RouterLink to="/cm">
        <button class="mainBtn b">
          <img class="mainIcon" src="../assets/.png" />
          <span class="mainTxt"></span>
        </button>
      </RouterLink>
      <RouterLink to="/gl">
        <button class="mainBtn b">
          <img class="mainIcon" src="../assets/.png" />
          <span class="mainTxt"></span>
        </button>
      </RouterLink>
    </div>

    <div class="btnBundles">
      <RouterLink to="/cal">
        <button class="mainBtn b">
          <img class="mainIcon" src="../assets/.png" />
          <span class="mainTxt"></span>
        </button>
      </RouterLink>
      <button class="mainBtn b">
        <img class="mainIcon" src="../assets/.png" />
        <span class="mainTxt">AI </span>
